body {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  /* background-color: rgb(202, 202, 123); */
  background-color: #efe3a1;
  font-family: 'Lato', sans-serif;
}

.header-container {
  height: 100vh;

  /* background-color: rgb(202, 202, 123); */
  background-color: #efe3a1;
  padding: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100vw;
  margin: 0;
}

.wrapper {
  padding: 20px 10px;
  /* background-color: white; */
  display: -webkit-flex;
  display: flex;
  padding: 10px 5px;
  margin: 20px 5px;
  width: 60vw;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.wrapper input[type='text'] {
  padding: 10px 25px;
  background: transparent;
  width: 60vw;
  border: none;
  line-height: 1.2em;
  border-bottom: 1px solid black;

  margin-right: 5px;

  /* border-radius: 50px 0 0 50px; */
  font-size: 18px;
  color: black;
}
.wrapper input[type='text'].error {
  border-bottom: 1px solid rgb(179, 58, 58);
}

.wrapper select {
  padding: 10px 5px;
  width: 20vw;
  border: none;
  margin-right: 5px;

  font-size: 18px;
}

.wrapper button {
  padding: 10px 5px;
  width: 20vw;
  border: none;
  margin-right: 5px;
  border-radius: 0 50px 50px 0;
  font-size: 18px;
  background-color: brown;
  color: white;
  cursor: pointer;
}

.wrapper input[type='text']:focus,
.wrapper button:focus {
  outline: none;
}

.submit-btn {
  transition: -webkit-transform 0.1s ease 0.1s;
  transition: transform 0.1s ease 0.1s;
  transition: transform 0.1s ease 0.1s, -webkit-transform 0.1s ease 0.1s;
}

.submit-btn:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

@media only screen and (min-width: 900px) {
  .wrapper {
    width: 700px;
  }
}

@media only screen and (max-width: 750px) {
  .wrapper {
    width: 100vw;
  }
}

