.card {
  color: green;
  padding: 20px;
  width: auto;
  /* height: 250px; */
  height: 200px;
  width: 200px;
  background-color: aliceblue;
  /* margin: 10px; */

  /* justify-content: center; */
  text-align: center;

  font-size: 40px;
  font-weight: 800;
  letter-spacing: 2px;
  cursor: pointer;
}

.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.card p {
  color: black;

  text-align: center;
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 2px;
}

.typoCards {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 20px 5px;
  margin-bottom: 10px;
}

.typo-card {
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 20px;
  min-width: 600px;
  width: auto;
  height: 500px;
  padding: 15px 10px;
  font-size: 100px;
  font-weight: 200;
  cursor: pointer;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.typo-card:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

@media only screen and (max-width: 800px) {
  .typo-card {
    min-width: 350px;
    font-size: 70px;
    margin: 10px;
  }
}

.back {
  position: fixed;
  top: 30px;
  left: 30px;
  z-index: 10;
}

.fontCards {
